import React from "react";
import styles from "./Contacts.module.scss";
import { Link } from "react-router-dom";

function Contacts() {
  return (
    <div className={styles.wrapper}>
      <h1>Контакти</h1>
      <p className={styles.breadCrumbs}>
        <Link to="/">Головна</Link> / Контакти
      </p>
      <div className={styles.contacts}>
        <div className={styles.phone}>
          <img src="data:image/png;base64,"></img>
          <p>Телефон</p>
          <p>+38096843769</p>
        </div>
        <div className={styles.email}>
          <img src="data:image/png;base64,"></img>
          <p>Еmail</p>
          <p>example@gmail.com</p>
        </div>
      </div>
      <iframe
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2539.646784926486!2d30.51915581567859!3d50.46630177947761!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x40d4ce3f9183f035%3A0x5f435e2a531c0853!2sEnglish%20School!5e0!3m2!1suk!2sua!4v1653771679270!5m2!1suk!2sua"
        width="600"
        height="450"
        loading="lazy"
        referrerpolicy="no-referrer-when-downgrade"
      ></iframe>
    </div>
  );
}

export default Contacts;
